import * as React from "react";
import { Col, PageHeader, Row } from "react-bootstrap";
import "./Resume.css";
// tslint:disable
class Resume extends React.Component<any, any> {
  constructor(props: any) {
    super(props);
  }
  timeAtCarahsoft() {
    const startDate: Date = new Date(2016, 2, 15);
    const now: Date = new Date();
    const years: number = now.getFullYear() - startDate.getFullYear();
    let months: number = now.getMonth() - startDate.getMonth();
    months += (now.getDate() >= startDate.getDate()) ? 1 : 0;
    return (
      <p>{years} Years and {months} Months</p>
    );
  }
  render() {
    return (
      <div className="about">
        <PageHeader>
          Resume
          </PageHeader>
        <Row className="flexRow">
          <Col xs={10} sm={8}>
            <p>
              Currently I'm <b>Data Science program manager</b> at&nbsp;
              <a target="blank" href="http://www.carahsoft.com/">Carahsoft Technology Corp.</a>
            </p>
            <p className="paragraph">
                I wear many hats which keeps me on my toes.
                Most of my job involves working with our <b>SQL</b> databases 
                (mostly T-SQL), which has earned me the nick-name "SQL-Wan Kenobi",   
                but most of all I enjoy programming.  
                Although my roots are with <b>python</b> I've come to trust the compiler and 
                do almost all of my work in either <b>C#</b> (framework and core) or <b>Typescript</b>.  
            </p>
            <p className="paragraph">
                My projects have been diverse; 
                creating a <b>web-app</b> to display stats on monitors placed around the office
                , building mail service applications
                , maintaining our <b>CRM</b>
                , administering <b>Windows</b> and <b>Linux</b> servers
                , working with <b>Tableau</b>/<b>Qlik</b>/<b>ZoomData</b>/<b>Splunk</b>/VBA in Excel
                , etc. etc. etc...

            </p>
          </Col>
          <Col xs={2} sm={4} className="timeSpan">
            <div>
              <p>2016-Present </p>
              {this.timeAtCarahsoft()}
            </div>
          </Col>
        </Row>
        <br />
        <Row className="flexRow">
          <Col xs={10} sm={8}>
            <p>
              Data sci cert
            </p>
            <p className="paragraph">
                what
            </p>
          </Col>
          <Col xs={2} sm={4} className="timeSpan">
            <div>
              <p>2016</p>
            </div>
          </Col>
        </Row>
      </div>
    );
  }
}

export { Resume };



